  position: absolute;
  bottom: 45px;
  left: 0;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: var(--overlay-color);
  transition: opacity var(--transition-params), visibility var(--transition-params);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: all .8s;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.open {
  opacity: 1;
  visibility: visible;
  pointer-events: visible;
}

.modal {
  width: 100vw;
  height: 100vh;
  background: var(--overlay-color);
  position: absolute;
  top: 0;
  left: 0;
}

@media screen and (min-width: 768px) {
  .modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

body.modal-open {
  overflow: hidden;
}

.modal-window {
  width: 280px;
  height: auto;
  background-color: var(--main-bg-color);
  position: absolute;
  top: 20px;
  bottom: 0;
  left: 50%;
  overflow: auto;
  transform: translateX(-50%);
}

@media screen and (min-width: 768px) {
  .modal-window {
    width: 704px;
    height: 552px;
    position: fixed;
    top: 50%;
    left: 50%;
    overflow: hidden;
    transform: translate(-50%, -50%);
  }
}

@media screen and (min-width: 1024px) {
