  width: 14px;
  height: 14px;
  color: var(--secondary-text-color);
  cursor: pointer;
  transition: color var(--transition-params);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  position: absolute;
  top: 16px;
  right: 16px;
}

.close-btn:hover {
