.pagination {
    display: flex;
    padding: 0;
    margin-right: auto;
    margin-left: auto;
    justify-content: center;
    @media screen and (max-width: $mobile), (min-width: $mobile) {
        width: 280px;
        // margin-top: 40px;
        margin-bottom: 40px;
    }
    @media screen and (min-width: $tablet) {
        width: 374px;
        // margin-top: 60px;
        margin-bottom: 60px;
    }
}
.pagination-list{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.pagination-button {
    display: flex;
    align-items: center;
    width: 40px;
    background-color: var(--secondary-bg-color);
    border-radius: 5px;
}
.pagination-item{
    display: flex;
    align-items: center;
    margin: 0;
    &:first-child, &:last-child,
    &:nth-child(2), &:nth-child(8) {
        @media screen and (max-width: $mobile), (min-width: $mobile) {
            display: none;
        }
        @media screen and (min-width: $tablet) {
            display: flex;
        }
    }
    &:nth-child(3) {
        @media screen and (max-width: $mobile), (min-width: $mobile) {
            margin-left: 10px;
        }
        @media screen and (min-width: $tablet) {
            margin-left: 0;
        }
    }
    &:nth-child(7) {
        @media screen and (max-width: $mobile), (min-width: $mobile) {
            margin-right: 10px;
        }
        @media screen and (min-width: $tablet) {
            margin-right: 0;
        }
    }
    &:hover a.pagination-link {
        color: var(--main-text-color);
        background-color: var(--accent-color);
        border-radius: 5px;
        transition: background-color var(--transition-params);
    }
    &.current {
        justify-content: center;
        width: 40px;
        height: 40px;
        margin: 0 8px;
        color: var(--main-text-color);
        background-color: var(--accent-color);
        border-radius: 5px;
        & a.pagination-link{
            color: var(--main-text-color);
        }
    }
}
.pagination-link{
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: var(--secondary-text-color);
    width: 32px;
    height: 32px;
    font-family: 'Roboto';
}